
import { defineComponent } from 'vue'
export default defineComponent({
  data() {
    return {
      opponent: '',
      playerName1: '',
      playerName2: '',
      difficulty: '',
      chosePlayer: false,
      choseCpu: true
    }
  },
  methods: {
    changeOpponent() {
      if (this.chosePlayer == true) {
        this.chosePlayer = false
        this.choseCpu = true
      } else {
        this.chosePlayer = true
        this.choseCpu = false
      }
    }
  }
})
